.Rate{width:200px;height:50px;display:flex;align-items:center;unicode-bidi:bidi-override;flex-direction:row-reverse}.Rate input{width:30px;height:30px;outline:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;cursor:pointer;line-height:30px;text-align:center;font-size:30px;transition:all .3s;box-sizing:border-box}.Rate input::after{content:"★";color:gray}.Rate input:hover::after{content:"★";color:gold;transition:all .3s ease 0s}.Rate input:hover~input::after{content:"★";color:gold;transition:all .3s ease 0s}.Rate input:checked::after{content:"★";color:gold;transition:all .3s ease 0s}.Rate input:checked~input::after{content:"★";color:gold;transition:all .3s ease 0s}.Rate input:hover{transform:scale(1.2)}